import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DashboardComponent } from './dashboard/dashboard.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { ProfileComponent } from './profile/profile.component';
import { UserManagementComponent } from './user-management/user-management.component';
import { NotificationManagementComponent } from './notification-management/notification-management.component';
import { SettingsComponent } from './settings/settings.component';
import { AuthGuard } from '../service/auth.guards';
import { UserMgmViewComponent } from './user-mgm-view/user-mgm-view.component';
import { UserMgmtEditComponent } from './user-mgmt-edit/user-mgmt-edit.component';

const routes: Routes = [
  
  { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
  {path: 'dashboard', component: DashboardComponent,canActivate:[AuthGuard]   },
  {path: 'change-password', component: ChangePasswordComponent,canActivate:[AuthGuard] },
  {path: 'edit-profile', component: EditProfileComponent,canActivate:[AuthGuard] },
  {path: 'profile', component: ProfileComponent,canActivate:[AuthGuard] },
  {path: 'user-management', component: UserManagementComponent,canActivate:[AuthGuard] },
  {path: 'notification-management', component: NotificationManagementComponent,canActivate:[AuthGuard] },
  {path: 'setting', component: SettingsComponent,canActivate:[AuthGuard]},
  {path: 'user-mgm-view/:id', component: UserMgmViewComponent},
  {path: 'user-mgmt-edit/:id', component: UserMgmtEditComponent},
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LayoutRoutingModule { }
