// import { Component, OnInit } from '@angular/core';
import { Component, OnInit, ViewChild } from "@angular/core";
import { DatepickerOptions } from 'ng2-datepicker';
import { MatPaginator, MatSort, MatTableDataSource } from "@angular/material";
import { AuthenticationService } from "../../service/authentication.service";
import * as enLocale from 'date-fns/locale/en';
import { MultiDataSet } from 'ng2-charts';
import { ChartType } from 'chart.js';
declare var $:any;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  dataSource = new MatTableDataSource

  public doFilter = (value: string) => {
    this.dataSource.filter = value.trim().toLocaleLowerCase();
  };

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  

  fromdate: Date;
  todate: Date;
  optionsFrom: DatepickerOptions = {
    minYear: 2017,
    maxDate: new Date(),
    locale: enLocale
  };
  optionsTo: DatepickerOptions = {
    // minDate: this.todate,
    maxDate: new Date(),
  };

  alldash: any
  forFilter: any
  dashList: any



  fromDate($event){
    this.todate = $event;
    this.optionsTo = {
      minDate: this.todate,
      maxDate: new Date(),
      locale: enLocale
    }
  }
  
  
  constructor(
    private api: AuthenticationService
  ) {
    this.fromdate = new Date();
    this.todate = new Date();
  }
  
  // Chart JS
  public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels:string[] = ['2015', '2016', '2017', '2018'];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = false;
  public barChartData:any[] = [
    {data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A'},
    {data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B'},
    {data: [48, 78, 50, 69, 56, 57, 40], label: 'Series c'}
  ];

  // events
  public chartClicked(e:any):void {
    console.log(e);
  }

  public chartHovered(e:any):void {
    console.log(e);
  }

  public randomize():void {
    // Only Change 3 values
    let data = [
      Math.round(Math.random() * 100),
      59,
      80,
      (Math.random() * 100),
      56,
      (Math.random() * 100),
      40];
    let clone = JSON.parse(JSON.stringify(this.barChartData));
    clone[0].data = data;
    this.barChartData = clone;    
  }

  // public doughnutChartLabels: Label[] = ['23', '45', '65', '46',];
  public doughnutChartData: MultiDataSet = [
    [240, 88, 40],
    [110, 177, 225],
    [251, 211, 31],
    [128, 195, 91],
  ];
  public doughnutChartType: ChartType = 'doughnut';

  ngOnInit() {

    this.getDashboardData()

    $('.lng-dropdown li a').click(function(){
      var aaa = this.innerHTML;
      $('.lng-dropdown #dropdown-active-item').html(aaa);
    });

  }

dashboardData: any;
data: any

getDashboardData(){
  console.log("hello");
  this.api.dashboardData({}).subscribe((res: any) =>{
    console.log(res, "res");
    this.dashboardData= res["response"]

    this.data = {
      totalUser: (this.dashboardData.totalUser && this.dashboardData.totalUser[0]) ? this.dashboardData.totalUser.length : "",
      totalRoom: (this.dashboardData.totalRoom && this.dashboardData.totalRoom[0]) ? this.dashboardData.totalRoom.length : "",
      
    }
    
  }),
  (err =>{
    console.log(err, "error");
    
  })
  
}
  


filterDate() {
  debugger
  console.log("Hhiiiiiiiiiiiiiiiiii", this.fromdate);
  if (this.fromdate && !this.todate) {
    console.log("Hhiiiiiiiiiiiiiiiiii", this.fromdate);
    var fromFilter = new Date(this.fromdate).getTime();
    var fromFilter = this.fromdate.getTime();

    this.alldash = this.dashList.filter(
      (item: any) => new Date(item.createdAt).getTime() >= fromFilter
    );
    console.log(
      this.alldash,
      ":::::::::::::",
      this.fromdate,
      ":::::::::::::",
      fromFilter
    );
    this.dataSource = new MatTableDataSource(this.alldash);
    if (this.forFilter) {
      this.dataSource.filter = this.forFilter.trim().toLowerCase();
    }
    this.dataSource.paginator = this.paginator;
  } else if (this.todate && !this.fromdate) {
    console.log(this.todate, ":::::::::::::");
    var toFilter = new Date(this.todate).getTime();
    //var toFilter = this.todate;

    this.alldash = this.dashList.filter(
      (item: any) => item.createdAt <= toFilter
    );
    this.dataSource = new MatTableDataSource(this.alldash);
    if (this.forFilter) {
      this.dataSource.filter = this.forFilter.trim().toLowerCase();
    }
    this.dataSource.paginator = this.paginator;
  } else {
    var fromFilter = new Date(this.fromdate).getTime();
    var toFilter = new Date(this.todate).getTime();
    // var fromFilter =this.fromdate
    // var toFilter = this.todate;

    console.log(fromFilter, "::::::::::::::", toFilter, "line asdas");
    this.alldash = this.dashList.filter((item: any) => {
      // console.log("Date :",new Date(item.createdAt).getTime());
      return (
        new Date(item.createdAt).getTime() <= toFilter &&
        new Date(item.createdAt).getTime() >= fromFilter
      );
    });
    console.log("Count :", this.alldash.length);
    this.dataSource = new MatTableDataSource(this.alldash);
    if (this.forFilter) {
      this.dataSource.filter = this.forFilter.trim().toLowerCase();
    }
    this.dataSource.paginator = this.paginator;
  }
}



}
