import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../service/authentication.service';
import { FormBuilder, FormGroup, Validators } from "@angular/forms";

declare var $:any;
@Component({
  selector: 'app-user-mgmt-edit',
  templateUrl: './user-mgmt-edit.component.html',
  styleUrls: ['./user-mgmt-edit.component.css']
})
export class UserMgmtEditComponent implements OnInit {
  edituserForm:FormGroup;

  userId: string;
  dataId:{};
  user: any;
  single: any[];
  picture: any;
  submitted
  constructor(
    private router: Router,
    private formBuilder: FormBuilder,   
    public route:ActivatedRoute,
    private api: AuthenticationService
  ) { }
  ngOnInit() {
    this.userId = this.route.snapshot.paramMap.get('id');
    console.log(this.userId, "jhbkjvkjvjl")
    this.getUserById(this.userId)
    $('.nav-tabs li a').on('click', function(){
      $('.nav-tabs li').removeClass('active');
      $(this).addClass('active');
    });
    this.edituserForm = this.formBuilder.group({
      Name: ['', Validators.required],
      Country: ['', Validators.required],
      countryCode: ['', Validators.required], 
      Mobile: ['', Validators.required], 
      Gender:['',Validators.required],
      DOB:['',Validators.required],
      Coin:['',Validators.required]
    });
  }
  pChange
  singleFiles1(event) {
    this.pChange = true
    const file1 = event.target.files[0]
   
    this.single = [];
    var singleFiles = event.target.files;
    if (singleFiles) {
      for (var file of singleFiles) {
        var singleReader = new FileReader();
        singleReader.onload = (e:any) => {
          this.single.push(e.target.result);
          document.getElementById("imagefile1").setAttribute('src',e.target.result);
        }
        singleReader.readAsDataURL(file);
      }
    }
    this.picture = event.target.files[0];
    console.log(this.picture,"::::::::::::::::::::::::::;singleFiles1 brooooooo");
  }
  dobDate
  getUserById(id){
    let data={
      id:id
    }
    
    console.log(this.userId)
    this.api.getUserById(data).subscribe(res=>{
      console.log(res, "jhvkcvkcv75765r76476776373753")
      this.user = res['response'];
      // var value = this.user.dob
      // var date = value.slice(0,-7)
      // var month = value.slice(3,-5)
      // var year = value.slice(5)

      // console.log(date,month,year)
      // new Date(2021 , 1 , 25);
      this.dobDate = new Date(this.user.dob).setHours(0,0,0,0)
      // console.log(this.user , new Date(this.dobDate), this.dobDate)
      this.edituserForm.patchValue({ 
        Name: this.user.fullName,
        Country : this.user.country,
        Gender : this.user.gender,
        countryCode: this.user.countryCode,
        Mobile: this.user.mobile,
        DOB: new Date(this.dobDate),
        Coin: this.user.myCoin
      }); 
    },err=>{
      console.log(err)
    })
  }
  get g(){
    return this.edituserForm.controls;
  }

  selectedGender
  gChange
  getGender(value) {
    this.gChange = true
    this.selectedGender = value
    console.log(this.selectedGender, "gender");
  }
  editUser(){
      this.submitted = true;
      let d = new Date(this.edituserForm.value.DOB)
      let dt = d.getDate();
      let mn = d.getMonth();
      mn++;
      let yy = d.getFullYear();
      let dobData = mn + "/" + dt + "/" + yy
      if(this.edituserForm.invalid){
        return;
      } 
      let a = new FormData();
      a.append('user_id',this.user._id)
      a.append('fullName',this.edituserForm.value.Name)
      a.append('country',this.edituserForm.value.Country)
      a.append('countryCode',this.edituserForm.value.countryCode)
      a.append('mobile',this.edituserForm.value.Mobile)
      a.append('gender',this.gChange ==true ? this.selectedGender :  this.user.gender)
      a.append('dob',dobData)
      a.append('myCoin',this.edituserForm.value.Coin)
      a.append('profileImage',   this.pChange ==true ? this.picture :  this.user.profileImage )
      this.api.editUser(a).subscribe(
        (editdata) => {
          console.log(editdata, "after editcurrency api");
          this.edituserForm.reset();
          this.api.toaster("success","Updated Successfully.")
          this.router.navigateByUrl('/home/user-management');
        },
        (error) => {
          console.log(error);
          console.log(error);
          this.api.toaster("error","Something went wrong.")
        }
      );
   
  }

}
